import { BrowserRouter, Switch, Route } from "react-router-dom";
import CoinDetailPage from "./pages/CoinDetailPage";
import HomePage from "./pages/HomePage";

const Router = () => {
  return (
    <BrowserRouter>
      {/* Switch:    Route , react-router-dom  6 Routes  */}
      <Switch>
        {/* /:coinId =>   URL          */}
        <Route path="/:coinId">
          <CoinDetailPage />
        </Route>
        <Route path="/">
          <HomePage />
        </Route>
      </Switch>
    </BrowserRouter>
  );
};

export default Router;
